import React from "react";
import "./Home.css"
import pic1 from "../components/images/venus.jpg";
import milkyway from "../components/images/milkyway.jpeg";
import psp from "../components/images/psp.jpeg"
import bgimg from "../components/images/backimg.png"
function Home() {
    return (
        <div className="container-fluid text-center bg-dark text-light">

            <div id="home" >
            <div className="container p-5">
            <h1 className="display-1 text-info text-uppercase pt-5" >Fly through the solar corona</h1>
            
            <p className="lead mt-5">A scientific mission to unlock the mysteries of the Sun's corona and solar wind <br />

A spacecraft that uses breakthrough technology and autonomy to endure heat and radiation like no other mission <br />

A unique solar observatory that will orbit within 4 million miles of our star's surface to directly study the formation of the solar wind <br />

The first mission named for a living scientist: Dr. Eugene Parker, who theorized the existence of the solar wind
On April 28, 2021, during its eighth flyby of the Sun, Parker Solar Probe encountered the specific magnetic and particle conditions at 18.8 solar radii (around 8.1 million miles) above the solar surface that told scientists it had crossed the Alfvén critical surface for the first time and finally entered the solar atmosphere. <br />The Parker Solar Probe (PSP) is a NASA space probe launched in 2018 with the mission of making observations of the outer corona of the Sun. It will approach to within 9.86 solar radii (6.9 million km or 4.3 million miles from the center of the Sun, and by 2025 will travel, at closest approach, as fast as 690,000 km/h (430,000 mph), or 0.064% the speed of light. It is the fastest object ever built.
</p>
            
            </div>
            </div>
            <div id="mission" className="pt-5">
            <div className="container pt-5">
            <h1>THE MISSION</h1>
            <p className="lead mt-5">For the first time in history, a spacecraft has touched the Sun. NASA’s Parker Solar Probe has now flown through the Sun’s upper atmosphere – the corona – and sampled particles and magnetic fields there. 
The new milestone marks one major step for Parker Solar Probe and one giant leap for solar science. Just as landing on the Moon allowed scientists to understand how it was formed, touching the very stuff the Sun is made of will help scientists uncover critical information about our closest star and its influence on the solar system. 
As it circles closer to the solar surface, Parker is making new discoveries that other spacecraft were too far away to see, including from within the solar wind – the flow of particles from the Sun that can influence us at Earth. In 2019, Parker discovered that magnetic zig-zag structures in the solar wind, called switchbacks, are plentiful close to the Sun. But how and where they form remained a mystery. Halving the distance to the Sun since then, Parker Solar Probe has now passed close enough to identify one place where they originate: the solar surface.
The first passage through the corona – and the promise of more flybys to come – will continue to provide data on phenomena that are impossible to study from afar.
<br /> <br /> Closer Than Ever Before 
Parker Solar Probe launched in 2018 to explore the mysteries of the Sun by traveling closer to it than any spacecraft before. Three years after launch and decades after first conception, Parker has finally arrived.
Unlike Earth, the Sun doesn’t have a solid surface. But it does have a superheated atmosphere, made of solar material bound to the Sun by gravity and magnetic forces. As rising heat and pressure push that material away from the Sun, it reaches a point where gravity and magnetic fields are too weak to contain it.
That point, known as the Alfvén critical surface, marks the end of the solar atmosphere and beginning of the solar wind. Solar material with the energy to make it across that boundary becomes the solar wind, which drags the magnetic field of the Sun with it as it races across the solar system, to Earth and beyond. Importantly, beyond the Alfvén critical surface, the solar wind moves so fast that waves within the wind cannot ever travel fast enough to make it back to the Sun – severing their connection. 
Until now, researchers were unsure exactly where the Alfvén critical surface lay. Based on remote images of the corona, estimates had put it somewhere between 10 to 20 solar radii from the surface of the Sun – 4.3 to 8.6 million miles. Parker’s spiral trajectory brings it slowly closer to the Sun and during the last few passes, the spacecraft was consistently below 20 solar radii (91 percent of Earth’s distance from the Sun), putting it in the position to cross the boundary – if the estimates were correct.
On April 28, 2021, during its eighth flyby of the Sun, Parker Solar Probe encountered the specific magnetic and particle conditions at 18.8 solar radii (around 8.1 million miles) above the solar surface that told scientists it had crossed the Alfvén critical surface for the first time and finally entered the solar atmosphere.
</p>
            <p className="lead mt-5">Parker Solar Probe will swoop to within 4 million miles of the Sun's surface, facing heat and radiation like no spacecraft before it. Launched on Aug. 12, 2018, Parker Solar Probe will provide new data on solar activity and make critical contributions to our ability to forecast major space-weather events that impact life on Earth.

In order to unlock the mysteries of the corona, but also to protect a society that is increasingly dependent on technology from the threats of space weather, we will send Parker Solar Probe to touch the Sun.

In 2017, the mission was renamed for Eugene Parker, the S. Chandrasekhar Distinguished Service Professor Emeritus, Department of Astronomy and Astrophysics at the University of Chicago. In the 1950s, Parker proposed a number of concepts about how stars—including our Sun—give off energy. He called this cascade of energy the solar wind, and he described an entire complex system of plasmas, magnetic fields, and energetic particles that make up this phenomenon. Parker also theorized an explanation for the superheated solar atmosphere, the corona, which is – contrary to what was expected by physics laws -- hotter than the surface of the Sun itself. This is the first NASA mission that has been named for a living individual.</p>

            </div>
            </div>
            <div id = "gallery">
                <div className="container">
                    <h1>GALLERY</h1>
                    <div className="row">
                        <div className="col-12 col-md-4" >
                        <div className = "gallery-section-card shadow text-center bg-secondary">
            <img
            src={pic1}
            className = "gallery-section-image"
          />
      <p className = "gallery-section-card-text">
      NASA’s Parker Solar Probe captured stunning views of Venus during its close flyby of the planet in July 2020.
      </p>
    </div>
                        </div>
                        <div className="col-12 col-md-4"  bg = "secondary">
                        <div className = "gallery-section-card shadow text-center bg-secondary">
            <img
            src={milkyway}
            className = "gallery-section-image"
          />
      <p className = "gallery-section-card-text">
      NThe image shows the first-light data from Parker Solar Probe's WISPR instrument suite. It shows Milky Way, looking at the galactic center.
      </p>
    </div>
                        </div>

                        <div className="col-12 col-md-4" >
                        <div className = "gallery-section-card shadow text-center bg-secondary">
            <img
            src={psp}
            className = "gallery-section-image"
          />
      <p className = "gallery-section-card-text">
      An image showing the parker solar probe spacecraft.
      </p>
    </div>
                        </div>
                    </div>



                </div>
            
    
  </div>
            </div>
      );
}

export default Home;